import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ConfirmationService, TreeNode } from 'primeng/primeng';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { OrgTreeBean } from './bean/org-tree.bean';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  selector: 'work-org-tree',
  templateUrl: './work-org-tree.component.html',
  styleUrls: ['./work-org-tree.component.scss'],
  providers: [CustomeHttpService, Commfunc]
})

/**
 * 机构对应关系
 */
export class WorkOrgTreeComponent implements OnInit {
  @Input() inValue: any;
  @Output() outValue: EventEmitter<any> = new EventEmitter();

  precredit: string = '机构树查询';
  precredit1: string = '机构详情';
  aa: boolean = false;
  test: any = [];
  orgEle: any;
  orgTreeBean: OrgTreeBean = new OrgTreeBean();
  treeOrgName: string;
  //初始机构号
  org: string;
  org1: string;
  //提示信息
  msgs: Message[] = [];
  date: any;
  files: TreeNode[];
  treeList2: string;
  date1: any;
  isAll: boolean = false;
  selectedFiles;
  user: any;
  orgId: string;
  constructor(
    public customeHttpService: CustomeHttpService,
    public commfunc: Commfunc,
  ) {
    this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    if (this.user.orgId) {
      this.orgId = this.user.orgId;
    }
  }

  ngOnInit() {
    this.org = this.orgId;
    this.query();
  }
  //选中
  nodeCheck(event) {
    this.treeOrgName = event.node.label;
    this.orgEle = { orgId: event.node.orgId, orgName: event.node.orgName, location: event.node.location }
    this.outValue.emit(this.orgEle);
  }

  //点击加号
  nodeExpend(event) {
    this.org = event.node.orgId;
    this.orgTreeBean.orgId = this.org;
    this.orgTreeBean.relationshipType = '-1';
    if (event.node.children && event.node.children.length > 0) {
      return;
    }
    this.customeHttpService.queryOrgByOrgIdAndRelationship(this.orgTreeBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        event.node.children = this.toTree(data.orgList);
      }
    })
  }

  toTree(data) {
    if (data) {
      data.forEach(item => {
        item['label'] = item['orgName']
        item['orgId'] = item['orgId']
        item['location'] = item['location']
      });
      return data
    }

  }

  //查询
  query() {
    this.orgTreeBean.orgId = this.org;
    this.orgTreeBean.relationshipType = '0';
    this.customeHttpService.queryOrgByOrgIdAndRelationship(this.orgTreeBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.date = this.toTree(data.orgList);
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'info', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  query1() {
    this.orgTreeBean.orgId = this.org1;
    this.orgTreeBean.relationshipType = '0';
    this.customeHttpService.queryOrgByOrgIdAndRelationship(this.orgTreeBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.date1 = this.toTree(data.orgList);
        this.query()
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'info', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  //清空
  cleanUp() {
    this.treeOrgName = '';
    this.orgEle = '';
  }
}