import { Component, OnInit } from '@angular/core';
import { CusOperationHttpService } from '../../http/custom-operation/custom-operation.http.service';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { Router } from '@angular/router';
import { WorkTaskProcessDetailQryBean } from './bean/workTaskProcessDetailQry.bean';
import { ActivatedRoute } from '@angular/router';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { tableMessage } from 'app/@uisftech/common/constant/config';
@Component({
    selector: 'app-manage-detail',
    templateUrl: './manage-detail.component.html',
    styleUrls: ['./manage-detail.component.scss'],
    providers: [CusOperationHttpService, Commfunc]
})
export class ManageDetailComponent implements OnInit {
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    code: any;
    first: number = 0;
    workTaskType: any;
    scheduleType: any;
    scheduleTypeOptions: any;
    precredit: string = '待办及提醒事项处理明细';
    //模态框的标题
    headerTitle: string = "修改";
    //模态框是否显示
    display: boolean = false;
    showModel: string;
    //修改的参数
    updateValue: any = [];
    //提示信息
    msgs: Message[] = [];
    //总页数
    total: string;
    //每页多少条
    pageSize: string;
    temp: any;

    workTaskReportOptions: any;//待办事项类型
    workTaskProcessDetailQryBean: WorkTaskProcessDetailQryBean = new WorkTaskProcessDetailQryBean();
    sales: any[];
    constructor(
        public httpService: CusOperationHttpService,
        public routeInfo: ActivatedRoute,
        public router: Router,
        public commfunc: Commfunc
    ) { }
    ngOnInit() {
        this.codeValues();
        this.workTaskType = this.code['WorkTaskType'];
        this.scheduleTypeOptions = this.code['ScheduleTypes'];//大类
        this.workTaskReportOptions = this.code['ItemsType'];//小类
        this.workTaskProcessDetailQryBean.pageNum = 1;
        this.workTaskProcessDetailQryBean.pageSize = 10;

        let items = this.routeInfo.snapshot.params['params'];
        this.temp = JSON.parse(items)
        this.sales = [];
        this.query();
    }
    toDetail(item) {
        this.router.navigate(["pages/tzb/custom/work-report/shenpi", item])
    }
    query() {
        this.workTaskProcessDetailQryBean.scheduleName = this.temp.scheduleName;
        this.workTaskProcessDetailQryBean.estimatedStartDt = this.temp.estimatedStartDt;
        this.workTaskProcessDetailQryBean.estimatedEndDt = this.temp.estimatedEndDt;
        this.workTaskProcessDetailQryBean.receiveOrgId = this.temp.receiveOrgId;
        this.workTaskProcessDetailQryBean.receiver = this.temp.receiver;
        this.workTaskProcessDetailQryBean.scheduleTypeParentId = this.temp.scheduleTypeParentId;
        this.workTaskProcessDetailQryBean.scheduleTypeId = this.temp.scheduleTypeId;
        this.workTaskProcessDetailQryBean.statusId = this.temp.statusId;
        this.httpService.workTaskProceMing(this.workTaskProcessDetailQryBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                for (let item of data.custListInfo) {
                    this.httpService.queryTellerById({ tellerId: item['receiver'] }).subscribe(data => {
                        if (data.tellerName && data.tellerName != '' && data.tellerName != null) {
                            item['receiver'] = data.tellerName;
                        }
                    });
                    this.httpService.queryOrgById({ orgId: item.receiveOrgId }).subscribe(data => {
                        if (data.orgName && data.orgName != null) {
                            item.receiveOrgId = data.orgName;
                        }
                    });
                    if (item.scheduleTypeParentId == '03') {
                        item.display = true;
                    } else {
                        item.display = false;
                    }
                }
                this.sales = data.custListInfo;
                this.total = data.totalNum;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        });
    }
    paginate(event) {
        //每页显示的条数
        this.workTaskProcessDetailQryBean.pageSize = event.rows * 1;
        //当前页
        this.workTaskProcessDetailQryBean.pageNum = event.page + 1;
        this.query();
    }
    //排序
    paixu(item) {
        this.workTaskProcessDetailQryBean.havingCondition = item;
        this.workTaskProcessDetailQryBean.receiveOrgId = this.temp.receiveOrgId;
        this.workTaskProcessDetailQryBean.statusId = this.temp.statusId;
        this.httpService.workTaskProceMing(this.workTaskProcessDetailQryBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.sales = data.custListInfo;
                this.total = data.totalNum;
                data.custListInfo.forEach(item => {
                    if (item.scheduleTypeParentId == '03') {
                        item.display = true;
                    } else {
                        item.display = false;
                    }
                });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        });
    }
    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //排序
    doSort(event) {
        console.log(event)
        // if (event.field == 'deptBal') {
        if (event.order == 1) {
            this.workTaskProcessDetailQryBean.havingCondition = 'field';
            this.query();
        }
        if (event.order == -1) {
            this.workTaskProcessDetailQryBean.havingCondition = 'field Desc';
            this.query();
        }
        //   }
    }

}