import {Routes,RouterModule} from '@angular/router';
import {ModuleWithProviders} from '@angular/core';
import {ContactSheetOrganizationComponent} from'./contact-sheet-organization/contact-sheet-organization.component';
import {ContactSheetStaffComponent} from './contact-sheet-staff/contact-sheet-staff.component';
import {ManageDetailComponent} from './manage-detail/manage-detail.component';
import {ReportOrganizationComponent} from './report-organization/report-organization.component';
import {ReportStaffComponent} from './report-staff/report-staff.component';
import {ShenPiComponent} from './shenpi/shenpi.component';
export const routes:Routes=[
    {
        path:'',
        children:[
            {
                path:'contact-sheet-organization',
                component:ContactSheetOrganizationComponent
            },
            {
                path:'contact-sheet-staff',
                component:ContactSheetStaffComponent
            },
            {
                path:'manage-detail',
                component:ManageDetailComponent
            },
            {
                path:'report-organization',
                component:ReportOrganizationComponent
            },
            {
                path:'report-staff',
                component:ReportStaffComponent
            },
            {
                path:'shenpi',
                component:ShenPiComponent
            }
        ]
    }
]
export const WorkReportRouting:ModuleWithProviders=RouterModule.forChild(routes);