import { Component, OnInit } from '@angular/core';
import { WorkTaskReportOrgQryBean } from './bean/workTaskReportOrgQry.bean';
import { CusOperationHttpService } from '../../http/custom-operation/custom-operation.http.service';
import { Router } from '@angular/router';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { ConfirmDialogModule, ConfirmationService, Message } from 'primeng/primeng';
import { TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
import { tableMessage } from 'app/@uisftech/common/constant/config';
@Component({
    selector: 'app-report-organization',
    templateUrl: './report-organization.component.html',
    styleUrls: ['./report-organization.component.scss'],
    providers: [CusOperationHttpService]
})
export class ReportOrganizationComponent implements OnInit {
    code: any;
    first: number = 0;
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    msgs: Message[] = [];
    temData: Date = new Date();
    orgTreePage: boolean = false;
    treeOrgName: any;
    treeOrgId: any;
    ch: any = CALENDAR_CN;
    estimatedStartDt: Date = null;//
    estimatedEndDt: Date = null;//
    precredit: string = '工作任务处理报表(机构)';
    userform: FormGroup;
    cols: any = [];
    sales: any = [];
    //总页数
    total: string;
    //每页多少条
    pageSize: string;
    workTaskType: any;
    scheduleTypeOptions: any;//待办任务列表
    workTaskReportOptions: any;//待办事项类型
    itemsType: any = [];//待办事项类型

    showWhich: number = 2;
    workTaskReportOrgQryBean: WorkTaskReportOrgQryBean = new WorkTaskReportOrgQryBean();
    constructor(
        public httpService: CusOperationHttpService,
        public router: Router,
        public fb: FormBuilder,
        public commfunc: Commfunc,
    ) {
        this.userform = fb.group({
            treeOrgName: ['', Validators.required],
            estimatedStartDt: ['', Validators.required],
            estimatedEndDt: ['', Validators.required],
            scheduleTypeParentId: ['', Validators.required],
            scheduleTypeId: [''],
            scheduleName: ['']
        })
    }
    ngOnInit() {
        this.codeValues();
        this.workTaskType = this.code['WorkTaskType'];
        this.scheduleTypeOptions = this.code['ScheduleTypes'];
        this.workTaskReportOptions = this.code['ItemsType'];
        this.workTaskReportOrgQryBean.pageNum = 1;
        this.workTaskReportOrgQryBean.pageSize = 10;
    }
    showData() {
        this.temData = this.estimatedStartDt;
    }
    showStart() {
        if (this.estimatedStartDt && this.estimatedStartDt != null && this.estimatedEndDt && this.estimatedEndDt != null) {
            if (this.estimatedStartDt.getTime() > this.estimatedEndDt.getTime()) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '开始日期必须小于到期日期!' }];
                setTimeout(() => {
                    this.estimatedStartDt = null;
                });
                return
            }
        }
    }
    showEnd() {
        if (this.estimatedStartDt && this.estimatedStartDt != null && this.estimatedEndDt && this.estimatedEndDt != null) {
            if (this.estimatedStartDt.getTime() > this.estimatedEndDt.getTime()) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '到期日期必须大于开始日期!' }];
                setTimeout(() => {
                    this.estimatedEndDt = null;
                });
                return
            }
        }
    }
    queryFirst() {
        this.workTaskReportOrgQryBean.pageNum = 1;
        this.workTaskReportOrgQryBean.pageSize = 10;
        this.first = 0;
        this.query();
    }
    //查询
    query() {
        //校验的
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }
        if (this.workTaskReportOrgQryBean.scheduleTypeParentId == '01') {
            this.showWhich = 1;
        } else if (this.workTaskReportOrgQryBean.scheduleTypeParentId == '02') {
            this.showWhich = 2;
        } else if (this.workTaskReportOrgQryBean.scheduleTypeParentId == '03') {
            this.showWhich = 3;
        }
        this.workTaskReportOrgQryBean.estimatedStartDt = this.commfunc.transDateN(this.estimatedStartDt);
        this.workTaskReportOrgQryBean.estimatedEndDt = this.commfunc.transDateN(this.estimatedEndDt);
        this.workTaskReportOrgQryBean.receiveOrgId = this.treeOrgId;
        this.workTaskReportOrgQryBean.receiveOrgName = this.treeOrgName;
        this.httpService.workTaskProceOrg(this.workTaskReportOrgQryBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.sales = data.custListInfo;
                this.total = data.totalNum;
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });

    }
    //跳转起详情页
    toDetail(item, data) {
        item['estimatedStartDt'] = this.commfunc.transDateN(this.estimatedStartDt);
        item['estimatedEndDt'] = this.commfunc.transDateN(this.estimatedEndDt);
        if (data == 'notDeal') {
            item['statusId'] = '01';
        } else if (data == 'beDeal') {
            item['statusId'] = '02';
        } else if (data == 'expiredNotDeal') {
            item['statusId'] = '03';
        } else if (data == 'expiredDeal') {
            item['statusId'] = '04';
        } else if (data == 'passed') {
            item['statusId'] = '05';
        } else if (data == 'unPassed') {
            item['statusId'] = '06';
        }
        let a = JSON.stringify(item)
        open("/#/pages/tzb/custom/work-report/manage-detail;params=" + encodeURI(a));
    }
    //翻页
    paginate(event) {
        //每页显示的条数
        this.workTaskReportOrgQryBean.pageSize = event.rows * 1;
        //当前页
        this.workTaskReportOrgQryBean.pageNum = event.page + 1;
        this.first = event.page * this.workTaskReportOrgQryBean.pageSize;
        this.query();
    }
    treeCall(param) {
        this.orgTreePage = false;
        this.treeOrgName = param.orgName;
        this.treeOrgId = param.orgId;
    }
    perTreeShow() {
        this.orgTreePage = true;
    }
    DownOut() {
        if (this.sales == []) {
            this.msgs = [];
            this.msgs = [{ severity: 'info', summary: '提示', detail: '请先查询!' }];
            return
        } else {
            this.httpService.workTaskProceOrgExportFile(this.workTaskReportOrgQryBean).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI(data.fileName) + "&fileUrl=" + data.fileUrl)
                    this.msgs = [];
                    this.msgs = [{ severity: 'info', summary: '提示', detail: '下载成功' }];
                } else {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                    return;
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
                return;
            });
        }

    }
    chongzhi() {
        this.workTaskReportOrgQryBean.scheduleTypeParentId = '';
        this.workTaskReportOrgQryBean.scheduleTypeId = '';
        this.treeOrgName = '';
        this.treeOrgId = '';
        this.estimatedStartDt = null;
        this.estimatedEndDt = null;
        this.workTaskReportOrgQryBean.scheduleName = '';
        this.workTaskReportOrgQryBean.receiveOrgId = '';
        this.workTaskReportOrgQryBean.receiveOrgName = '';
    }
    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }
    //待办任务切换
    itemsChange() {
        if (this.workTaskReportOrgQryBean.scheduleTypeParentId == "") {
            this.itemsType = [];
            this.workTaskReportOrgQryBean.scheduleTypeId = '';
        } else {
            let params = {
                taskTypeParentId: this.workTaskReportOrgQryBean.scheduleTypeParentId,
                taskTypeKey: 'itemsType'
            }
            this.httpService.taskTypeQry(params).subscribe(data => {
                let arr = data.taskTypeList;
                this.itemsType = [];
                this.workTaskReportOrgQryBean.scheduleTypeId = '';
                arr.forEach(item => {
                    this.itemsType.push({ label: item.taskTypeName, value: item.taskTypeId })
                });
            });
        }
    }
    typeChange() {
        if (!this.workTaskReportOrgQryBean.scheduleTypeParentId || this.workTaskReportOrgQryBean.scheduleTypeParentId == '') {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '请先选择事项大类!' }];
        }
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

}