import { Component, OnInit } from '@angular/core';
import { WorkTaskReportEmpQryBean } from './bean/workTaskReportEmpQry.bean';
import { QueryUserBean } from './bean/queryUser.bean';
import { CusOperationHttpService } from '../../http/custom-operation/custom-operation.http.service';
import { Router } from '@angular/router';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { ConfirmDialogModule, ConfirmationService, Message } from 'primeng/primeng';
import * as _ from 'lodash';
import { TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { CommonHttpService } from "app/pages/tzb/common/http/common.http.service";
@Component({
    selector: 'app-report-staff',
    templateUrl: './report-staff.component.html',
    styleUrls: ['./report-staff.component.scss'],
    providers: [CusOperationHttpService, ConfirmationService, CommonHttpService]
})
export class ReportStaffComponent implements OnInit {
    code: any;
    //选择数据
    chooseData: any = [];
    checkAll: boolean = false;
    selectedCities: any;
    headerTitle: string = "柜员同步";
    //模态框是否显示
    display: boolean = false;
    //模态框
    showModel: string;
    tableData: any;
    tableData1: any
    first: any = 0;
    parent_value: any;
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    personId: any = [];
    personName: any = [];//员工
    personPage: boolean = false;
    cities: any = [
        { label: '11', value: '1' }, { label: '22', value: '2' }, { label: '33', value: '3' },
    ];
    temData: Date = new Date();
    msgs: Message[] = [];
    orgTreePage: boolean = false;
    treeOrgName: any;
    treeOrgId: any;
    ch: any = CALENDAR_CN;
    estimatedStartDt: Date = null;//
    estimatedEndDt: Date = null;//
    precredit: string = '工作任务处理报表(员工)';
    userform: FormGroup;
    cols: any[];
    sales: any[];
    //总页数
    totaltable: number;
    total: number;
    //每页多少条
    pageSize: string;
    workTaskType: any;
    scheduleTypeOptions: any;//待办任务列表
    itemsType: any = [];//待办事项类型
    workTaskReportOptions: any;//待办任务
    queryUserBean: QueryUserBean = new QueryUserBean();
    workTaskReportEmpQryBean: WorkTaskReportEmpQryBean = new WorkTaskReportEmpQryBean();
    user: any;
    user2: any;
    orgId: string = '';
    userId: string = '';
    userName: string = '';
    orgName: string = '';
    extParam: any;
    user3: any;

    showPage: boolean = true;
    showWhich: number = 2;
    constructor(
        public router: Router,
        public httpService: CusOperationHttpService,
        public fb: FormBuilder,
        public commfunc: Commfunc,
        public commonHttpService: CommonHttpService,
        public confirmationService: ConfirmationService,
    ) {
        this.userform = fb.group({
            treeOrgName: ['', Validators.required],
            personName: [''],
            estimatedStartDt: ['', Validators.required],
            estimatedEndDt: ['', Validators.required],
            scheduleTypeParentId: ['', Validators.required],
            scheduleTypeId: [''],
            scheduleName: [''],
        });
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.user2 = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        this.user3 = this.commfunc.getSessionDataCH('dropDownSelect');
        if (this.user.orgId) {
            this.orgId = this.user.orgId;
        }
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
        if (this.user2.userName) {
            this.userName = this.user2.userName;
        }
        if (this.user2.orgName) {
            this.orgName = this.user2.orgName;
        }
        if (this.user.extParam) {
            this.extParam = this.user.extParam;
        }
        if (this.extParam.managePermission == 'N') {
            if (this.user3) {
                this.treeOrgName = this.user3.split(',')[0];
            } else {
                this.treeOrgName = this.orgName;
            }
            this.treeOrgId = this.extParam.orgId;
            this.personName = this.userName;
            this.personId = [];
            this.personId.push(this.userId);
            this.showPage = false;
        } else {
            this.treeOrgName = '';
            this.treeOrgId = '';
            this.personName = '';
            this.personId = [];
            this.showPage = true;
        }
    }
    ngOnInit() {
        this.codeValues();
        this.workTaskType = this.code['WorkTaskType'];
        this.scheduleTypeOptions = this.code['ScheduleTypes'];
        this.workTaskReportOptions = this.code['ItemsType'];
        this.workTaskReportEmpQryBean.pageNum = 1;
        this.workTaskReportEmpQryBean.pageSize = 10;
        this.queryUserBean.pageSize = 10;
        this.queryUserBean.pageNum = 1;
    }
    transDate(value) {
        let time = new Date(value);
        let year = value.getFullYear();
        let month = value.getMonth() + 1 + '';
        let data = value.getDate() + '';
        if (month.length < 2) {
            month = '0' + month;
        }
        if (data.length < 2) {
            data = "0" + data
        }
        let newtime = year + "" + month + "" + data;
        return newtime;
        // let  time = new Date(value);
        // let  time1=value.getFullYear() +""+ (value.getMonth() + 1)+''+ value.getDate();
    }
    showData() {
        this.temData = this.estimatedStartDt;
    }
    showStart() {
        if (this.estimatedStartDt && this.estimatedStartDt != null && this.estimatedEndDt && this.estimatedEndDt != null) {
            if (this.estimatedStartDt.getTime() > this.estimatedEndDt.getTime()) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '开始日期必须小于到期日期!' }];
                setTimeout(() => {
                    this.estimatedStartDt = null;
                });
                return
            }
        }
    }
    showEnd() {
        if (this.estimatedStartDt && this.estimatedStartDt != null && this.estimatedEndDt && this.estimatedEndDt != null) {
            if (this.estimatedStartDt.getTime() > this.estimatedEndDt.getTime()) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '到期日期必须大于开始日期!' }];
                setTimeout(() => {
                    this.estimatedEndDt = null;
                });
                return
            }
        }
    }
    queryFirst() {
        this.workTaskReportEmpQryBean.pageNum = 1;
        this.workTaskReportEmpQryBean.pageSize = 10;
        this.first = 0;
        this.query();
    }
    //查询
    query() {
        //校验的
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }
        //01提醒  02工作任务  03审批
        if (this.workTaskReportEmpQryBean.scheduleTypeParentId == '01') {
            this.showWhich = 1;
        } else if (this.workTaskReportEmpQryBean.scheduleTypeParentId == '02') {
            this.showWhich = 2;
        } else if (this.workTaskReportEmpQryBean.scheduleTypeParentId == '03') {
            this.showWhich = 3;
        }
        this.workTaskReportEmpQryBean.receiver = this.personId;
        this.workTaskReportEmpQryBean.estimatedStartDt = this.commfunc.transDateN(this.estimatedStartDt);
        this.workTaskReportEmpQryBean.estimatedEndDt = this.commfunc.transDateN(this.estimatedEndDt);
        this.workTaskReportEmpQryBean.receiveOrgId = this.treeOrgId;
        this.workTaskReportEmpQryBean.receiveOrgName = this.treeOrgName;
        this.httpService.workProceMing(this.workTaskReportEmpQryBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.sales = data.custListInfo;
                this.totaltable = data.totalNum;
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }
    //跳转详情
    toDetail(item, data) {
        item['estimatedStartDt'] = this.commfunc.transDateN(this.estimatedStartDt);
        item['estimatedEndDt'] = this.commfunc.transDateN(this.estimatedEndDt);
        if (data == 'notDeal') {
            item['statusId'] = '01';
        } else if (data == 'beDeal') {
            item['statusId'] = '02';
        } else if (data == 'expiredNotDeal') {
            item['statusId'] = '03';
        } else if (data == 'expiredDeal') {
            item['statusId'] = '04';
        } else if (data == 'passed') {
            item['statusId'] = '05';
        } else if (data == 'unPassed') {
            item['statusId'] = '06';
        }
        let a = JSON.stringify(item)
        open("/#/pages/tzb/custom/work-report/manage-detail;params=" + encodeURI(a));
    }
    paginate(event) {
        //每页显示的条数
        this.workTaskReportEmpQryBean.pageSize = event.rows * 1;
        //当前页
        this.workTaskReportEmpQryBean.pageNum = event.page + 1;
        this.first = event.page * this.workTaskReportEmpQryBean.pageSize;
        this.query();

    }
    treeCall(param) {
        this.orgTreePage = false;
        this.treeOrgName = param.orgName;
        this.treeOrgId = param.orgId;
        this.parent_value = param.orgId;
    }
    perTreeShow() {
        this.orgTreePage = true;
    }
    DownOut() {
        this.httpService.workProceMingExportFile(this.workTaskReportEmpQryBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI(data.fileName) + "&fileUrl=" + data.fileUrl)
                this.msgs = [];
                this.msgs = [{ severity: 'info', summary: '提示', detail: '下载成功' }];
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                return;
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
            return;
        });
    }
    chongzhi() {
        if (this.showPage == false) {
            this.treeOrgName = this.orgName;
            this.treeOrgId = this.orgId;
            this.personName = this.userName;
            this.personId = [];
            this.personId.push(this.userId);
        } else {
            this.treeOrgName = '';
            this.treeOrgId = '';
            this.personId = [];
            this.personName = [];
        }
        this.workTaskReportEmpQryBean.scheduleTypeParentId = '';
        this.workTaskReportEmpQryBean.scheduleTypeId = '';
        this.estimatedStartDt = null;
        this.estimatedEndDt = null;
        this.workTaskReportEmpQryBean.receiver = [];
        this.workTaskReportEmpQryBean.scheduleName = '';
        this.workTaskReportEmpQryBean.receiveOrgId = '';
        this.workTaskReportEmpQryBean.receiveOrgName = '';
    }
    perShow() {
        if (this.treeOrgId == '' || !this.treeOrgId) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '机构号为空,请选择机构' }];
            return;
        } else {
            this.queryClick();
            this.personPage = true;
        }
    }
    perInfor(item) {
        this.personPage = false;
        let arr = [];
        let arrlist = [];
        item.forEach(temp => {
            arr.push(temp.tellerName);
            arrlist.push(temp.tellerId);
        });
        this.personName = arr;
        this.personId = arrlist;
    }
    querySecond() {
        this.queryUserBean.pageSize = 10;
        this.queryUserBean.pageNum = 1;
        this.first = 0;
        this.queryClick();
    }
    queryClick() {
        this.queryUserBean.orgId = this.treeOrgId;
        this.commonHttpService.queryTellerByCombConditions(this.queryUserBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data.tellerList;
                /**
                 * 2018.1.15--
                 */
                let tmpTotal = data.total;
                this.total = tmpTotal * 1;
                /**
                 * --2018.1.15
                 */
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }
    // 重置
    reset() {
        this.queryUserBean = new QueryUserBean()
    }

    // 增加
    followMe() {
        this.headerTitle = '柜员同步';
        this.display = true;
        this.showModel = '1';
    }

    //添加后返回的值
    addCall(param) {
        this.display = param;
        this.queryUserBean.pageNum = 1;
        this.queryClick();
    }
    //全选按钮
    checkAllClick() {
        this.chooseData = [];
        for (let i = 0; i < this.tableData.length; i++) {
            if (this.checkAll == true) {
                this.tableData[i].checkBox = true;
                this.chooseData.push(this.tableData[i]);
            }
            else {
                this.chooseData = [];
                this.tableData[i].checkBox = false;
            }
        }
    }
    //单选按钮
    checkBoxClick(data) {
        if (data.checkBox) {
            this.chooseData.push(data)
        } else {
            this.chooseData.forEach((item, i) => {
                if (item == data) {
                    this.chooseData.splice(i, 1)
                }
            });
        }
        if (this.chooseData.length == this.tableData.length) {
            this.checkAll = true;
        } else {
            this.checkAll = false;
        }
    }
    addBtnCom() {
        let listCustNo = [];
        let listCustName = [];
        this.chooseData.forEach(item => {
            listCustNo.push(item.tellerId);
            listCustName.push(item.tellerName);
        });
        this.personName = listCustName;
        this.personId = listCustNo;
        this.personPage = false;
    }
    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }
    //待办任务切换
    itemsChange() {
        if (this.workTaskReportEmpQryBean.scheduleTypeParentId == "") {
            this.itemsType = [];
            this.workTaskReportEmpQryBean.scheduleTypeId = '';
        } else {
            let params = {
                taskTypeParentId: this.workTaskReportEmpQryBean.scheduleTypeParentId,
                taskTypeKey: 'itemsType'
            }
            this.httpService.taskTypeQry(params).subscribe(data => {
                let arr = data.taskTypeList;
                this.itemsType = [];
                this.workTaskReportEmpQryBean.scheduleTypeId = '';
                arr.forEach(item => {
                    this.itemsType.push({ label: item.taskTypeName, value: item.taskTypeId })
                });
            });
        }
    }
    typeChange() {
        if (!this.workTaskReportEmpQryBean.scheduleTypeParentId || this.workTaskReportEmpQryBean.scheduleTypeParentId == '') {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '请先选择事项大类!' }];
        }
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

}