import { Component, OnInit } from '@angular/core';
import { CusOperationHttpService } from '../../http/custom-operation/custom-operation.http.service';
import { CustContactReportEmpQryBean } from './bean/custContactReportEmpQry.bean';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { ConfirmDialogModule, ConfirmationService, Message } from 'primeng/primeng';
import * as _ from 'lodash';
import { TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { CommonHttpService } from "app/pages/tzb/common/http/common.http.service";
import { QueryUserBean } from './bean/queryUser.bean';
@Component({
    selector: 'app-contact-sheet-staff',
    templateUrl: './contact-sheet-staff.component.html',
    styleUrls: ['./contact-sheet-staff.component.scss'],
    providers: [CusOperationHttpService, ConfirmationService, CommonHttpService]
})
export class ContactSheetStaffComponent implements OnInit {
    code: any;
    first: number = 0;
    //选择数据
    chooseData: any = [];
    checkAll: boolean = false;
    selectedCities: any;
    headerTitle: string = "柜员同步";
    //模态框是否显示
    display: boolean = false;
    //模态框
    showModel: string;
    tableData: any;
    tableData1: any
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    personId: any = [];
    personName: any = [];//员工
    personPage: boolean = false;
    contactsNo: any;
    msgs: Message[] = [];
    orgTreePage: boolean = false;
    treeOrgName: any;
    treeOrgId: any;
    ch: any = CALENDAR_CN;
    dealDate: any;//统计月度
    showMonthDate: boolean = false;
    precredit: string = '客户联系报表(员工)';
    userform: FormGroup;
    sales: any[];
    cols: any[];
    cities: any = [
        { label: '11', value: '1' }, { label: '22', value: '2' }, { label: '33', value: '3' },
    ];
    //总页数
    totaltable: number;
    total: string;
    //每页多少条
    pageSize: string;
    //联系类型
    contactType: any;
    //走访方式
    interviewType: any;
    parent_value: any;
    queryUserBean: QueryUserBean = new QueryUserBean();
    custContactReportEmpQryBean: CustContactReportEmpQryBean = new CustContactReportEmpQryBean();
    user: any;
    user2: any;
    orgId: string = '';
    userId: string = '';
    userName: string = '';
    orgName: string = '';
    extParam: any;

    orgNameShow: boolean = false;
    userNameShow: boolean = false;
    constructor(
        public httpService: CusOperationHttpService,
        public fb: FormBuilder,
        public commfunc: Commfunc,
        public commonHttpService: CommonHttpService,
        public confirmationService: ConfirmationService,
    ) {
        this.userform = fb.group({
            treeOrgName: ['', Validators.required],
            personName: ['', Validators.required],
        });
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.user2 = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        if (this.user.orgId) {
            this.orgId = this.user.orgId;
        }
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
        if (this.user2.userName) {
            this.userName = this.user2.userName;
        }
        if (this.user2.orgName) {
            this.orgName = this.user2.orgName;
        }
        if (this.user.extParam) {
            this.extParam = this.user.extParam;
            console.log(this.extParam)
        }
        if (this.extParam.managePermission == 'N') {
            this.treeOrgName = this.orgName;
            this.treeOrgId = this.orgId;
            this.orgNameShow = true;
            this.personName = this.userName;
            this.personId = this.userId;
            this.userNameShow = true;
        } else {
            this.treeOrgName = '';
            this.treeOrgId = '';
            this.orgNameShow = false;
            this.personName = '';
            this.personId = '';
            this.userNameShow = false;
        }
    }
    ngOnInit() {
        this.codeValues();
        this.contactType = this.code['ContactType'];
        this.interviewType = this.code['InterviewType'];
        this.custContactReportEmpQryBean.pageNum = 1;
        this.custContactReportEmpQryBean.pageSize = 10;
        this.queryUserBean.pageSize = 10;
        this.queryUserBean.pageNum = 1;
        this.cols = [
            { field: 'no', header: '排名' },
            { field: 'orgId', header: '机构号' },
            { field: 'contactOrg', header: '机构名称' },
            { field: 'contactsNo', header: '员工号' },
            { field: 'contactsName', header: '员工名称' },
            { field: 'contactNum', header: '联络客户数' },
            { field: 'ditributeNum', header: '管户客户数' },
            { field: 'contactReportNum', header: '联络报告笔数' },
            { field: 'contactNumV', header: '联络客户覆盖率' },
            { field: 'contactReportNumV', header: '联络报告覆盖率' },
            { field: 'contactTheme', header: '联系类型' },
            { field: 'contactWay', header: '走访方式' },
            { field: 'contactStatus', header: '联系状态' },
        ];
    }
    //日期转化
    transDate(value) {
        let time = new Date(value);
        let year = value.getFullYear();
        let month = value.getMonth() + 1 + '';
        let data = value.getDate() + '';
        if (month.length < 2) {
            month = '0' + month;
        }
        if (data.length < 2) {
            data = "0" + data
        }
        let newtime = year + "-" + month;
        return newtime;
    }
    paginate(event, item) {
        if (item == 'table') {
            //每页显示的条数
            this.custContactReportEmpQryBean.pageSize = event.rows * 1;
            //当前页
            this.custContactReportEmpQryBean.pageNum = event.page + 1;
            this.first = event.page * this.custContactReportEmpQryBean.pageSize;
            this.queryStaff();
        } else if (item == 'teller') {
            this.queryUserBean.pageSize = event.rows * 1;
            //当前页
            this.queryUserBean.pageNum = event.page + 1;
            this.first = event.page * this.queryUserBean.pageSize;

            //调用查询的方法
            this.queryClick();
        }
    }
    queryFirst() {
        this.custContactReportEmpQryBean.pageNum = 1;
        this.custContactReportEmpQryBean.pageSize = 10;
        this.first = 0;
        this.queryStaff();
    }
    //查询
    queryStaff() {
        //校验的
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }
        if (!this.dealDate || this.dealDate == '') {
            this.showMonthDate = true;
        } else {
            this.showMonthDate = false;
            this.custContactReportEmpQryBean.contactsNo = this.personId;
            this.custContactReportEmpQryBean.dealDate = this.dealDate;
            this.custContactReportEmpQryBean.orgId = this.treeOrgId;
            this.httpService.customerContactNo(this.custContactReportEmpQryBean).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.sales = data.custInfo;
                    this.totaltable = data.totalNum;
                } else {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
            });
        }
    }
    treeCall(param) {
        this.orgTreePage = false;
        this.treeOrgName = param.orgName;
        this.treeOrgId = param.orgId;
        this.parent_value = param.orgId;
    }
    perTreeShow() {
        this.orgTreePage = true;
    }
    DownOut() {
        this.httpService.customerContactNoExportFile(this.custContactReportEmpQryBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                open(TZB_CUS_DOWNLOAD + "?fileName=" + data.fileName + "&fileUrl=" + data.fileUrl)
                this.msgs = [];
                this.msgs = [{ severity: 'info', summary: '提示', detail: '下载成功' }];
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                return;
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
            return;
        });
    }
    chongzhi() {
        this.custContactReportEmpQryBean.contactsNo = [];
        this.custContactReportEmpQryBean.contactTheme = "";
        this.custContactReportEmpQryBean.contactWay = "";
        this.treeOrgName = "";
        this.dealDate = "";
        this.personId = [];
        this.personName = [];
        this.custContactReportEmpQryBean.contactsNo = [];
    }
    perShow() {
        if (this.treeOrgId == '' || !this.treeOrgId) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '机构号为空,请选择机构' }];
            return;
        } else {
            this.queryUserBean.orgId = this.treeOrgId;
            this.queryClick();
            this.personPage = true;
        }
    }
    perInfor(item) {
        this.personPage = false;
        let arr = [];
        let arrlist = [];
        item.forEach(temp => {
            arr.push(temp.tellerName);
            arrlist.push(temp.tellerId);
        });
        this.personName = arr;
        this.personId = arrlist;
    }
    querySecond() {
        this.queryUserBean.pageSize = 10;
        this.queryUserBean.pageNum = 1;
        this.first = 0;
        this.queryClick();
    }
    //员工查询
    queryClick() {
        this.commonHttpService.queryTellerByCombConditions(this.queryUserBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data.tellerList;

                this.total = data.total;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }
    // 重置
    reset() {
        this.queryUserBean = new QueryUserBean()
    }

    // 增加
    followMe() {
        this.headerTitle = '柜员同步';
        this.display = true;
        this.showModel = '1';
    }

    //添加后返回的值
    addCall(param) {
        this.display = param;
        this.queryUserBean.pageNum = 1;
        this.queryClick();
    }
    //全选按钮
    checkAllClick() {
        this.chooseData = [];
        for (let i = 0; i < this.tableData.length; i++) {
            if (this.checkAll == true) {
                this.tableData[i].checkBox = true;
                this.chooseData.push(this.tableData[i]);
            }
            else {
                this.chooseData = [];
                this.tableData[i].checkBox = false;
            }
        }
    }
    //单选按钮
    checkBoxClick(data) {
        if (data.checkBox) {
            this.chooseData.push(data)
        } else {
            this.chooseData.forEach((item, i) => {
                if (item == data) {
                    this.chooseData.splice(i, 1)
                }
            });
        }
        if (this.chooseData.length == this.tableData.length) {
            this.checkAll = true;
        } else {
            this.checkAll = false;
        }
    }
    addBtnCom() {
        let listCustNo = [];
        let listCustName = [];
        this.chooseData.forEach(item => {
            listCustNo.push(item.tellerId);
            listCustName.push(item.tellerName);
        });
        this.personName = listCustName;
        this.personId = listCustNo;
        this.personPage = false;
    }
    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

}