import { Component, OnInit } from '@angular/core';
import { CustContactReportOrgQryBean } from './bean/custContactReportOrgQry.bean';
import { CusOperationHttpService } from '../../http/custom-operation/custom-operation.http.service';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { ConfirmDialogModule, ConfirmationService, Message } from 'primeng/primeng';
import { TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
import { tableMessage } from 'app/@uisftech/common/constant/config';
@Component({
    selector: 'app-contact-sheet-organization',
    templateUrl: './contact-sheet-organization.component.html',
    styleUrls: ['./contact-sheet-organization.component.scss'],
    providers: [CusOperationHttpService]
})
export class ContactSheetOrganizationComponent implements OnInit {
    code: any;
    first: number = 0;
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    msgs: Message[] = [];
    orgTreePage: boolean = false;
    treeOrgName: any;
    treeOrgId: any;
    ch: any = CALENDAR_CN;
    dealDate: any;//统计月度
    showMonthDate: boolean = false;
    precredit: string = '客户联系报表(机构)';
    userform: FormGroup;
    sales: any[];
    cols: any[];
    //总页数
    total: string;
    //每页多少条
    pageSize: string;
    //联系类型
    contactType: any;
    //走访方式
    interviewType: any;
    custContactReportOrgQryBean: CustContactReportOrgQryBean = new CustContactReportOrgQryBean();
    constructor(
        public httpService: CusOperationHttpService,
        public fb: FormBuilder,
        public commfunc: Commfunc
    ) {
        this.userform = fb.group({
            treeOrgName: ['', Validators.required],
        })
    }
    ngOnInit() {
        this.codeValues();
        this.contactType = this.code['ContactType'];
        this.interviewType = this.code['InterviewType'];
        this.custContactReportOrgQryBean.pageNum = 1;
        this.custContactReportOrgQryBean.pageSize = 10;
        this.cols = [
            { field: 'no', header: '排名' },
            { field: 'orgId', header: '机构号' },
            { field: 'contactOrg', header: '机构名称' },
            { field: 'contactNum', header: '联络客户数' },
            { field: 'ditributeNum', header: '管户客户数' },
            { field: 'contactReportNum', header: '联络报告笔数' },
            { field: 'contactNumV', header: '联络客户覆盖率' },
            { field: 'contactReportNumV', header: '联络报告覆盖率' },
            { field: 'contactTheme', header: '联系主题' },
            { field: 'contactWay', header: '联系方式' },
            { field: 'contactStatus', header: '联系状态' },

        ];
    }
    //日期转化
    transDate(value) {
        let time = new Date(value);
        let year = value.getFullYear();
        let month = value.getMonth() + 1 + '';
        let data = value.getDate() + '';
        if (month.length < 2) {
            month = '0' + month;
        }
        if (data.length < 2) {
            data = "0" + data
        }
        let newtime = year + "-" + month;
        return newtime;
    }
    chongzhi() {
        this.custContactReportOrgQryBean.contactTheme = '';
        this.custContactReportOrgQryBean.contactWay = '';
        this.treeOrgName = '';
    }
    queryFirst() {
        this.custContactReportOrgQryBean.pageNum = 1;
        this.custContactReportOrgQryBean.pageSize = 10;
        this.first = 0;
        this.queryOrganization();
    }
    //查询
    queryOrganization() {
        //校验的
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }
        if (!this.dealDate || this.dealDate == '') {
            this.showMonthDate = true;
        } else {
            this.showMonthDate = false;
            console.log(this.dealDate)
            this.custContactReportOrgQryBean.dealDate = this.dealDate;
            this.custContactReportOrgQryBean.orgId = this.treeOrgId;
            this.httpService.customerContactOrg(this.custContactReportOrgQryBean).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.sales = data.custInfo;
                    this.total = data.totalNum;
                } else {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
            });
        }
    }
    paginate(event) {
        //每页显示的条数
        this.custContactReportOrgQryBean.pageSize = event.rows * 1;
        //当前页
        this.custContactReportOrgQryBean.pageNum = event.page + 1;
        this.first = event.page * this.custContactReportOrgQryBean.pageSize;
        this.queryOrganization();
    }
    treeCall(param) {
        this.orgTreePage = false;
        this.treeOrgName = param.orgName;
        this.treeOrgId = param.orgId;
    }
    perTreeShow() {
        this.orgTreePage = true;
    }
    DownOut() {
        this.httpService.customerContactOrgExportFile(this.custContactReportOrgQryBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                open(TZB_CUS_DOWNLOAD + "?fileName=" + data.fileName + "&fileUrl=" + data.fileUrl)
                this.msgs = [];
                this.msgs = [{ severity: 'info', summary: '提示', detail: '下载成功' }];
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
                return;
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
            return;
        });


    }
    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

}