import { Component, OnInit } from '@angular/core';
import { CusOperationHttpService } from '../../http/custom-operation/custom-operation.http.service';
import { Router } from '@angular/router';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { ConfirmDialogModule, ConfirmationService, Message } from 'primeng/primeng';
import { ActivatedRoute } from '@angular/router';
import { ScheduleProcessQryBean } from './bean/scheduleProcessQry.bean';
// import { ShenPi } from '../../constant/custOperation.codeValue';
@Component({
    selector: 'app-shenpi',
    templateUrl: './shenpi.component.html',
    styleUrls: ['./shenpi.component.scss'],
    providers: [CusOperationHttpService, Commfunc]
})
export class ShenPiComponent implements OnInit {
    code: any;
    first: number = 0;
    shenPiOptions: any;
    //总页数
    total: string;
    //每页多少条
    pageSize: number;
    sales: any = [];
    scheduleId: string;
    scheduleName: string;
    actualCompletionDate: any;
    receiverName: string;
    statusId: string;
    createdStamp: any;
    lastUpdatedStamp: any;
    msgs: Message[] = [];
    ch: any = CALENDAR_CN;
    scheduleProcessQryBean: ScheduleProcessQryBean = new ScheduleProcessQryBean();
    constructor(
        public routeInfo: ActivatedRoute,
        public httpService: CusOperationHttpService,
        public commfunc: Commfunc,
    ) {
    }
    ngOnInit() {
        this.codeValues();
        this.shenPiOptions = this.code['ShenPi'];
        this.scheduleProcessQryBean.pageSize = 10;
        this.scheduleProcessQryBean.pageNum = 1;
        this.query();
    }
    query() {
        this.first = 0;
        this.scheduleProcessQryBean.scheduleId = this.routeInfo.snapshot.params.scheduleId;
        this.httpService.scheduleProcessQry(this.scheduleProcessQryBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.sales = data.scheduleProcessList;
                this.total = data.totalNum;
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
        });
    }
    paginate(event) {
        //每页显示的条数
        this.scheduleProcessQryBean.pageSize = event.rows * 1;
        //当前页
        this.scheduleProcessQryBean.pageNum = event.page + 1;
        this.query();
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
}